import React, { useEffect, useCallback, useState} from 'react';
import {REMIX_COLLECTION_ADDRESS} from 'constants/zpore-addresses.js';
import {getSporeRemixes} from 'spores/getSporeRemixes.js';
import {connect} from 'react-redux';
import ZorbRemixView from './ZorbRemixView.js';
import './remixes.scss';

const ZorbRemixes = (props) => { 
    let [remixes, setRemixes] = useState([]);
    let [show, setShow] = useState(false);

    useEffect(() => {
        getSporeRemixes(REMIX_COLLECTION_ADDRESS, 1)
            .then(result => {
                setRemixes(result);
            });
    }, []);

    if (show) {
        return <ZorbRemixView hide={() => setShow(false)} remixes={remixes}/>;
    }
    return (
        <div
          onClick={() => setShow(true)}
          className="remixes-button">
          <span>{remixes.length} Remixes 𓍊</span>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZorbRemixes);
