import React, { memo, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const HEIGHT = 100;
const SECONDS_PER_STEP = (bpm) => {
    // how many steps in a minute?
    
    // there are 8 steps per beat so multiply by 8
    let steps_per_min = bpm*8;

    // there are 60 seconds per minute so multiply by 60
    let steps_per_second = steps_per_min / 60;

    // invert

    return 1/steps_per_second;
};
export default memo(({
    width,
    silence,
    buffer,
    bpm=90,
    waveform,
    start,
    length,
    height=100,
    startStep,
}) => { 

    let [pts, setPts] = useState([]);

    useEffect(() => {
        let x = [];

        for (let i=0 ; i < waveform.length; i += 4) {
            x.push(waveform[i]*(height*.35)/HEIGHT);
        }
        setPts(x);
    }, [waveform, setPts]);

    let _start = start/100*pts.length;


    let duration = buffer[0].length/44100;

    // how many steps does this make?
    
    let ratioOffset = silence  / duration;
    let offset = ratioOffset*width*2;


    // figure out how many beats to divide by assuming 90 beats per minute

    // how many minutes
    let durationMin = duration / 60;
    let beatCount = (durationMin * bpm)/2;
    let beatWidth = width*2/beatCount; // width of 1 beat in pixels

    // offset by startSteps % to 32

    let sps = SECONDS_PER_STEP(bpm);
    let modOffsetSeconds = (32 - (startStep % 32) + 8)*sps;

    let addiitional = (2*width)*(modOffsetSeconds / duration);

    if (startStep === 0) {
    } else {
        offset += addiitional;
    }

    // how much of steps to offset by


    let beats = [];
    for (let i=0; i < beatCount; i++) {
        beats.push(i);
    }


    const isInside = (i) => {
        return 100*(offset + i*beatWidth)/(2*width) >= start && 100*(offset+i*beatWidth)/(2*width) <= start + length;
    };
    return (
        <svg width={width*2} height={height}>
          {pts.map((y, i) => <rect className={(i >= _start && i <= _start + length/100*pts.length ? "sliced-region" : "") + (i%2===0 ? " even " : " odd")} x={2*i/pts.length*width} y={height/2-y/2} height={y} width={2}/>)}
          {beats.map((x,i) =>
              <rect
                x={offset + i*beatWidth}
                y={i%2 === 0 ? height-(isInside(i) ? 30 : 15): height-(isInside(i) ? 20 : 10)}
                height={(i%2===0 ? 30 : 20)*(isInside(i) ? 1 : 0.5)}
                width={2}
                className={isInside(i) ? "beat inside" : "beat"}/>)}
        </svg>
    );
});

